import React from 'react';
import ReactDOM from 'react-dom';
import * as utils from './utils.js';


export default class Card extends React.Component {

    constructor(props) {
        super(props);
        let values = props.value;
        let color = '#000';
        if(values.color === 'R'){
            color = '#FC0D1B';
        }

        this.state = {
            value: values.value,
            suite: values.suite,
            text: values.text,
            color: color
        }
    }

    render() {
        return (
            <div className="card-container">
                
            </div>
        )
    }


}


